.preview {
	border: 7px solid $blue;
	position: relative;
	display: block;
	width: 100%;
	margin-bottom: 60px;
	color: $dark;
	text-decoration: none;
	background-repeat: no-repeat;
	background-position: right bottom;
	&:hover {
		opacity: 0.7;
	}
	&__content {
		padding: 67px 30px;
		width: 66%;
	}
	&__title {
		margin-bottom: 30px;
		font-weight: 700;
		font-size: 24px;
	}
	&__discript {
		font-size: 18px;
	}
}